.faq {
  padding: 120px 0 122px;
  margin: 0;
  background-color: $color-ghost-white;

  @include vp-1199 {
    padding: 100px 0 99px;
  }

  @include vp-767 {
    padding: 70px 0;
  }
}

.faq__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 1160px;
  margin: 0 auto;
  padding: 0;

  @include vp-1199 {
    width: 678px;
  }

  @include vp-767 {
    width: 290px;
  }

  h2 {
    padding: 0;
    margin: 0;
    margin-bottom: 39px;
    font-family: $font-family-primery;
    font-weight: $font-weight-semibold;
    font-size: 54px;
    line-height: 65px;
    letter-spacing: 1.62px;
    width: 100%;
    align-self: flex-start;

    @include vp-1199 {
      margin-bottom: 41px;
      font-size: 42px;
      line-height: 50px;
      letter-spacing: 1.26px;
      text-align: center;
    }

    @include vp-767 {
      margin-bottom: 30px;
      font-size: 32px;
      line-height: 38px;
      letter-spacing: 0.96px;
      text-align: center;
    }
  }
}

.faq__content-wrapper {
  margin: 0 auto;
  padding: 0;
  display: grid;
  grid-template-columns: 260px 1fr;
  column-gap: 40px;
  width: 100%;

  @include vp-1199 {
    display: flex;
    flex-direction: column;
  }
}

.faq__tabs-list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  width: 100%;
  list-style-type: none;

  @include vp-1199 {
    display: grid;
    grid-template-columns: repeat(4, 162px);
    column-gap: 10px;
    row-gap: 10px;
    margin-bottom: 40px;
  }

  @include vp-767 {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 8px;
    row-gap: 8px;
    margin-bottom: 30px;
  }

  li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 18px 18px;
    outline: 1px solid rgba(16, 38, 84, 0.2);
    outline-offset: -1px;
    margin-bottom: 10px;
    font-weight: $font-weight-medium;
    font-size: 18px;
    line-height: 100%;
    cursor: pointer;

    @include vp-1199 {
      margin-bottom: 0;
    }

    @include vp-767 {
      font-size: 16px;
      padding: 13px 13px 15px 13px;
    }

    &:hover {
      outline: 1px solid $color-sapphire-blue;
      outline-offset: -1px;
    }

    &.is-open {
      background-image: $red-to-blue;
      color: $color-default-white;
      outline: 1px solid transparent;
      outline-offset: -1px;
    }

    &:focus-visible {
      outline: 1px solid $color-sapphire-blue;
      outline-offset: -1px;
    }

    @include hover-focus {
      @include vp-1199 {
        outline: 1px solid $color-sapphire-blue;
        outline-offset: -1px;
      }
    }

    &.no-active {
      outline: 1px solid rgba(16, 38, 84, 0.2);
      outline-offset: -1px;
      color: $color-link-water-grey;
      pointer-events: none;
      cursor: default;
    }
  }
}

.faq__description-wrapper {
  padding: 0;
  margin: 0;

  ul {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    list-style-type: none;
    width: 100%;
  }

  li {
    margin: 0;
    padding: 0;
    border-top: 1px solid rgba(28, 51, 116, 0.2);
    padding-top: 25px;
    padding-bottom: 24px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;

    @include vp-767 {
      padding-top: 10px;
      padding-bottom: 18px;
      grid-template-columns: 1fr 32px;
      column-gap: 20px;
    }

    &:last-child {
      border-bottom: 1px solid rgba(28, 51, 116, 0.2);
    }

    &.is-resize-padding-bottom {
      padding-bottom: 16px;

      @include vp-1199 {
        padding-bottom: 19px;
      }

      @include vp-767 {
        padding-bottom: 7px;
      }
    }
  }
}

.faq__question-button-wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 8px;

  @include vp-1199 {
    margin-bottom: 6px;
  }

  b {
    padding: 0;
    margin: 0;
    font-weight: $font-weight-medium;
    font-size: 24px;
    line-height: 120%;
    display: flex;
    flex-wrap: wrap;
    align-self: center;
    max-width: 800px;

    @include vp-1199 {
      max-width: 630px;
    }

    @include vp-767 {
      display: inline-block;
      padding-top: 10px;
      font-size: 18px;
      margin-bottom: 7px;
      max-width: 238px;
    }
  }

  br {
    display: none;

    @include vp-1199 {
      display: block;
    }

    @include vp-767 {
      display: none;
    }
  }

  .faq__products-break {
    display: none;

    @include vp-767 {
      display: block;
    }
  }

  button {
    padding: 0;
    margin: 0;
    width: 42px;
    height: 42px;
    border-radius: 50%;
    background-color: transparent;
    position: relative;
    border: 1px solid $color-sapphire-blue;
    align-self: center;
    cursor: pointer;

    @include vp-767 {
      width: 32px;
      height: 32px;
    }

    &::before {
      position: absolute;
      content: "";
      background-color: $color-sapphire-blue;
      width: 20px;
      height: 1px;
      transform: translateX(-50%) translateY(-50%);

      @include vp-767 {
        width: 15px;
      }
    }

    &::after {
      position: absolute;
      content: "";
      background-color: $color-sapphire-blue;
      width: 1px;
      height: 20px;
      transform: translateX(-50%) translateY(-50%);

      @include vp-767 {
        height: 15px;
      }
    }

    &.is-open {
      background-color: $color-sapphire-blue;

      &::before {
        background-color: $color-default-white;
      }

      &::after {
        width: 0;
        height: 0;
      }
    }

    &:hover {
      background-color: $color-sapphire-blue;

      &::before,
      &::after {
        background-color: $color-default-white;
      }
    }

    &:focus-visible {
      background-color: $color-sapphire-blue;

      &::before,
      &::after {
        background-color: $color-default-white;
      }
    }

    @include hover-focus {
      @include vp-1199 {
        background-color: $color-sapphire-blue;

        &::before,
        &::after {
          background-color: $color-default-white;
        }
      }
    }

    &:disabled {
      opacity: 0.3;
      cursor: default;
    }
  }
}

.faq__description {
  margin: 0;
  padding: 0;

  p {
    margin: 0;
    padding: 0;
    max-width: 660px;
    font-weight: $font-weight-light;
    font-size: 18px;
    line-height: 140%;

    @include vp-1199 {
      max-width: 558px;
    }

    @include vp-767 {
      font-size: 16px;
      max-width: 270px;
    }
  }
}
